import React, { useState } from 'react';
import { question } from './questions';

const Card = ({ question, selected, onSelect }) => {
  return (
    <div className="card">
      <h2 className="card-question">{question.number}.{question.content}</h2>
      <h3 className='card-answer'>
        {[1,2,3,4,5].map((num) => (
          <button 
            key={num} 
            onClick={() => onSelect(num)}
            style={{ backgroundColor: selected === num ? 'lightblue' : 'white' }}
          >
            {num}
          </button>
        ))}
      </h3>
    </div>
  );
};

export default function Home() {
  const [answers, setAnswers] = useState<number[]>(Array(question.length).fill(0));

  const handleAnswer = (index: number, num: number) => {
    const newAnswers = [...answers];
    newAnswers[index] = num;
    setAnswers(newAnswers);
  };


  const payload = answers.map((ans,idx) => ({
      number: idx+1,
      answer: ans
    }));
  
  const handlesend = async () => {
    fetch("http://127.0.0.1:8000/answers",{
    method: "POST",
    headers: {"Content-type":"application/json"},
    body: (JSON.stringify({answers:payload}))
  })
    const response = await fetch("http://127.0.0.1:8000/answers");
    const data = await response.json();
    if (data.statas == "error"){
      alert("未回答があります！");
    }
}

  return (
    <>
      <h1>RISCタイプ診断</h1>
      {question.map((q, idx) => (
        <Card 
          key={q.number} 
          question={q} 
          selected={answers[idx]} 
          onSelect={(num) => handleAnswer(idx, num)} 
        />
      ))}
      <button onClick={handlesend}>"結果を見る"</button>
    </>
  );
}
